import { MapPin, Eye, Heart, TrendingUp, Sparkles, Clock } from 'lucide-react'
import { Card, CardContent } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Button } from '@/components/ui/button'

interface JobCardProps {
  title: string
  company: string
  location: string
  experience: string
  salary: string
  tags: string[]
  isNew?: boolean
  isPopular?: boolean
  views: number
  likes: number
  deadline?: string
  isFavorited?: boolean
}

export function JobCard({
  title,
  company,
  location,
  experience,
  salary,
  tags,
  isNew,
  isPopular,
  views,
  likes,
  deadline,
  isFavorited,
}: JobCardProps) {
  return (
    <Card className="hover:shadow-md transition-shadow cursor-pointer">
      <CardContent className="p-4 space-y-3">
        <div className="flex items-start justify-between gap-2">
          <div className="flex-1 space-y-1">
            <div className="flex items-center gap-2">
              <h3 className="font-semibold text-base leading-tight">{title}</h3>
              {isNew && (
                <Badge variant="secondary" className="bg-emerald-100 text-emerald-700 border-emerald-200 h-5 px-1.5">
                  <Sparkles className="h-3 w-3 mr-0.5" />
                  신규
                </Badge>
              )}
              {isPopular && (
                <Badge variant="secondary" className="bg-orange-100 text-orange-700 border-orange-200 h-5 px-1.5">
                  <TrendingUp className="h-3 w-3 mr-0.5" />
                  인기
                </Badge>
              )}
            </div>
            <p className="text-sm text-muted-foreground">{company}</p>
          </div>
          <Button variant="ghost" size="icon" className="h-8 w-8 shrink-0">
            <Heart className={`h-4 w-4 ${isFavorited ? 'fill-red-500 text-red-500' : ''}`} />
          </Button>
        </div>

        <div className="flex flex-wrap gap-1.5">
          {tags.map((tag) => (
            <Badge key={tag} variant="outline" className="text-xs px-2 py-0.5">
              {tag}
            </Badge>
          ))}
        </div>

        <div className="flex items-center gap-3 text-xs text-muted-foreground">
          <span className="flex items-center gap-1">
            <MapPin className="h-3 w-3" />
            {location}
          </span>
          <span>{experience}</span>
          {deadline && (
            <span className="flex items-center gap-1">
              <Clock className="h-3 w-3" />
              {deadline}
            </span>
          )}
        </div>

        <div className="flex items-center justify-between pt-2 border-t">
          <span className="text-sm font-semibold text-primary">{salary}</span>
          <div className="flex items-center gap-3 text-xs text-muted-foreground">
            <span className="flex items-center gap-1">
              <Eye className="h-3 w-3" />
              {views.toLocaleString()}
            </span>
            <span className="flex items-center gap-1">
              <Heart className="h-3 w-3" />
              {likes}
            </span>
          </div>
        </div>
      </CardContent>
    </Card>
  )
}
